.AboutBlock {
    min-height: auto;
    background-color: #111;
    display: flex;
    align-items: center;
}

.AboutContainer {
    box-sizing: border-box;
    align-items: flex-end;
}

.ImgContainer {
    width: 50%;
    height: 470px;
}

.Img {
    height: 375px;
    width: 415px;
    border-radius: 5px;
    vertical-align: middle;
    object-fit: cover;
}

.TextContainer {
    width: 50%;
    height: auto;
}

.SpanAbout {
    font-family: "Poppins", serif;
    font-weight: 600;
    font-style: italic;
    display: inline-block;
    padding-top: 10px;
    padding-bottom: 35px;
    color: #bbb;
    font-size: 15px;
    line-height: 30px;
    letter-spacing: 0.2px;
}

.Text {
    padding-top: 32px;
    margin-bottom: 40px;
    width: 545px;
    border-top: 1px solid rgba(255,255,255,.1);

    font-style: italic;
    color: #bbb;
    font-family: "Mulish", serif;
    font-size: 15px;
    line-height: 30px;
    letter-spacing: 0.2px;
    font-weight: 400;
}

@media (max-width: 1040px) {
    .ImgContainer {
        width: 100%;
        height: 360px;
    }
    .TextContainer {
        width: 100%;
    }
    .SpanAbout {
        padding-top: 50px;
    }
    .Text {
        width: 310px;
    }
    .Img {
        display: flex;
        justify-content: center;
        height: 310px;
        width: 310px;
    }
}